@layer base {
  :root {
    color-scheme: dark;
  }
  body {
    background-color: var(--color-bg);
    color: var(--color-text);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  a {
    color: var(--color-accent);
    transition-property: color, background-color, border-color, outline-color, text-decoration-color, fill, stroke, --tw-gradient-from, --tw-gradient-via, --tw-gradient-to;
    transition-timing-function: var(--tw-ease, var(--default-transition-timing-function));
    transition-duration: var(--tw-duration, var(--default-transition-duration));
    &:hover {
      @media (hover: hover) {
        color: var(--color-accent-strong);
      }
    }
  }
  ::selection {
    background: rgba(34, 211, 238, 0.35);
    color: #f9fafb;
  }
}
:root {
  --gt-bg: 0 0 0;
  --gt-ink: 230 255 242;
  --gt-g: 0 255 140;
  --gt-c: 0 255 255;
  --gt-m: 255 51 204;
  --gt-line: 0 255 140;
  --gt-dim: 140 180 160;
}
html, body {
  height: 100%;
}
body {
  background: rgb(var(--gt-bg));
  color: rgb(var(--gt-ink));
}
.gt-body {
  background: rgb(var(--gt-bg));
  color: rgb(var(--gt-ink));
}
.gt-root {
  position: relative;
  min-height: 100vh;
  overflow: hidden;
}
.gt-skip {
  position: absolute;
  left: 14px;
  top: 12px;
  padding: 10px 12px;
  border: 1px solid rgba(0,255,140,0.35);
  background: rgba(0,0,0,0.78);
  color: rgba(230,255,242,0.95);
  border-radius: 10px;
  transform: translateY(-160%);
  transition: transform 160ms ease;
  z-index: 60;
}
.gt-skip:focus {
  transform: translateY(0);
  outline: none;
  box-shadow: 0 0 0 3px rgba(0,255,140,0.22);
}
.gt-bg {
  pointer-events: none;
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at 18% 22%, rgba(255,51,204,0.14), transparent 52%),
    radial-gradient(circle at 78% 70%, rgba(0,255,255,0.12), transparent 56%),
    radial-gradient(circle at 55% 48%, rgba(0,255,140,0.10), transparent 60%),
    linear-gradient(rgba(0,255,140,0.10) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0,255,140,0.08) 1px, transparent 1px),
    repeating-linear-gradient(to bottom, rgba(255,255,255,0.06) 0px, rgba(255,255,255,0.06) 1px, transparent 2px, transparent 6px),
    radial-gradient(ellipse at center, transparent 0%, rgba(0,0,0,0.55) 64%, rgba(0,0,0,0.92) 100%);
  background-size: auto,
    auto,
    auto,
    64px 64px,
    64px 64px,
    auto,
    auto;
  filter: blur(0px);
  opacity: 1;
}
.gt-app {
  position: relative;
  z-index: 10;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.gt-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  padding: 14px 16px;
  border-bottom: 1px solid rgba(0,255,140,0.18);
  background: rgba(0,0,0,0.72);
  backdrop-filter: blur(14px);
  box-shadow: 0 0 70px rgba(0,255,140,0.10);
}
.gt-brand {
  display: flex;
  align-items: center;
  gap: 12px;
  min-width: 0;
}
.gt-mark {
  width: 14px;
  height: 14px;
  border-radius: 3px;
  background: rgba(0,255,140,0.95);
  box-shadow: 0 0 12px rgba(0,255,140,0.85), 0 0 34px rgba(0,255,140,0.25);
}
.gt-title {
  font-weight: 900;
  letter-spacing: 0.22em;
  color: rgba(0,255,140,0.95);
  text-shadow: 0 0 8px rgba(0,255,140,0.85), 0 0 24px rgba(0,255,140,0.40), 0 0 70px rgba(0,255,140,0.16);
}
.gt-sub {
  margin-top: 3px;
  font-size: 12px;
  letter-spacing: 0.16em;
  color: rgba(230,255,242,0.62);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.gt-actions {
  display: flex;
  align-items: center;
  gap: 10px;
}
.gt-pill {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 8px 10px;
  font-size: 11px;
  font-weight: 900;
  letter-spacing: 0.16em;
  border: 1px solid rgba(0,255,140,0.22);
  background: rgba(0,0,0,0.55);
  color: rgba(0,255,255,0.70);
}
.gt-pillDim {
  color: rgba(230,255,242,0.55);
  border-color: rgba(0,255,140,0.16);
}
.gt-led {
  width: 10px;
  height: 10px;
  border-radius: 999px;
  background: rgba(0,255,140,0.95);
  box-shadow: 0 0 12px rgba(0,255,140,0.85), 0 0 34px rgba(0,255,140,0.25);
}
.gt-grid {
  flex: 1;
  display: grid;
  grid-template-columns: 220px 1fr 260px;
  min-height: 0;
}
.gt-nav {
  border-right: 1px solid rgba(0,255,140,0.14);
  background: rgba(0,0,0,0.55);
  backdrop-filter: blur(12px);
  padding: 14px 12px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.gt-link {
  display: block;
  padding: 10px 10px;
  border: 1px solid rgba(0,255,140,0.14);
  background: rgba(0,0,0,0.40);
  color: rgba(230,255,242,0.80);
  letter-spacing: 0.18em;
  font-weight: 800;
  text-decoration: none;
}
.gt-link:hover {
  border-color: rgba(0,255,140,0.30);
  box-shadow: 0 0 26px rgba(0,255,140,0.10);
}
.gt-navMeta {
  margin-top: auto;
  padding-top: 12px;
  border-top: 1px solid rgba(0,255,140,0.12);
}
.gt-metaRow {
  display: flex;
  justify-content: space-between;
  padding: 8px 6px;
  font-size: 11px;
  letter-spacing: 0.16em;
  color: rgba(230,255,242,0.55);
}
.gt-main {
  min-height: 0;
  padding: 18px;
  background: rgba(0,0,0,0.30);
}
.gt-side {
  border-left: 1px solid rgba(0,255,140,0.14);
  background: rgba(0,0,0,0.55);
  backdrop-filter: blur(12px);
  padding: 14px 12px;
}
.gt-panelTitle {
  font-weight: 900;
  letter-spacing: 0.22em;
  color: rgba(0,255,140,0.82);
  text-shadow: 0 0 10px rgba(0,255,140,0.35);
}
.gt-panelLine {
  margin: 12px 0;
  height: 1px;
  background: linear-gradient(90deg, rgba(0,255,140,0.0), rgba(0,255,140,0.35), rgba(0,255,255,0.28), rgba(0,255,140,0.0));
}
.gt-panelText {
  font-size: 12px;
  line-height: 1.6;
  letter-spacing: 0.08em;
  color: rgba(230,255,242,0.62);
}
.gt-foot {
  padding: 10px 16px 14px;
  border-top: 1px solid rgba(0,255,140,0.14);
  background: rgba(0,0,0,0.70);
  backdrop-filter: blur(12px);
}
.gt-footText {
  font-size: 12px;
  letter-spacing: 0.12em;
  color: rgba(230,255,242,0.55);
}
@media (max-width: 1024px) {
  .gt-grid {
    grid-template-columns: 220px 1fr;
  }
  .gt-side {
    display: none;
  }
}
@media (max-width: 780px) {
  .gt-grid {
    grid-template-columns: 1fr;
  }
  .gt-nav {
    display: none;
  }
}
@media (prefers-reduced-motion: reduce) {
  .gt-bg {
    background-position: 0 0;
  }
}
